<h1 id="小微商户配置" tabindex="-1"><a class="header-anchor" href="#小微商户配置" aria-hidden="true">#</a> 小微商户配置</h1>
<h2 id="关注功能配置" tabindex="-1"><a class="header-anchor" href="#关注功能配置" aria-hidden="true">#</a> 关注功能配置</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">merchantConfig</span><span class="token operator">-></span><span class="token function">setFollowConfig</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$subAppId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$subscribeAppId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$receiptAppId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$subMchId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>注意：<code>subscribe_appid</code>，<code>receipt_appid</code> 两个参数二选一，两个都填的话SDK默认选第一个，具体请参考小微商户专属文档</p>
</blockquote>
<h2 id="开发配置新增支付目录" tabindex="-1"><a class="header-anchor" href="#开发配置新增支付目录" aria-hidden="true">#</a> 开发配置新增支付目录</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">merchantConfig</span><span class="token operator">-></span><span class="token function">addPath</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$jsapiPath</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$appId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$subMchId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="新增对应appid关联" tabindex="-1"><a class="header-anchor" href="#新增对应appid关联" aria-hidden="true">#</a> 新增对应APPID关联</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">merchantConfig</span><span class="token operator">-></span><span class="token function">bindAppId</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$subAppId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$appId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$subMchId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="开发配置查询" tabindex="-1"><a class="header-anchor" href="#开发配置查询" aria-hidden="true">#</a> 开发配置查询</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">merchantConfig</span><span class="token operator">-></span><span class="token function">getConfig</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$subMchId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$appId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>以上接口调用过 <code>setSubMchId</code> 方法并且两个参数都传入过 则无需传入 <code>sub_mch_id</code> 和 <code>appid</code> 参数</p>
</blockquote>
